.BigCards {
  margin-left: 21px;
  position: relative;
  display: flex;
  width: 575px;
  height: 211px;
  background: #ffffff;
  box-shadow: 7px 10px 24px rgba(255, 199, 31, 0.22);
  border-radius: 12px;
}

.BigCardText {
  margin-left: 22px;
  width: 150px;
  display: flex;
  flex-direction: column;
}

.BigTitle {
  margin-bottom: 0px;
  width: 212px;
  height: 58px;
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}
.BigName {
  margin-bottom: 18px;
  width: 259px;
  height: 17px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #868686;
}
.BigButton {
  position: relative;
  display: flex;
  width: 212px;
  height: 60px;
  background: #fff;
  border-radius: 18px;
  color: #f73d27;
  border: none;
}
.BigButtonText {
  position: relative;
  display: flex;
  width: 212px;
  height: 60px;
}
.BigButton:hover .BigButtonText {
  display: none;
}
.BigButton:hover:before {
  display: flex;
  justify-content: center;
  content: "Add  🛒";
  font-weight: 900;
  font-size: 30px;
  line-height: 48px;
  border-radius: 10px;
  width: 212px;
  background-color: #f73d27;
  color: #fff;
  box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.BigButtonPLN {
  position: relative;
  top: -20px;
  font-weight: 700;
  font-size: 14px;
  line-height: 17px;
}
.BigButtonPrice {
  width: 138px;
  height: 48px;
  font-weight: 900;
  font-size: 40px;
  line-height: 48px;
  color: #f73d27;
  transform: rotate(-12.16deg);
}
.BigCardCircle {
  position: absolute;
  top: 13px;
  right: 13px;
  width: 154px;
  height: 154px;
  background: #ffc71f;
  border-radius: 50%;
}

.BigCardImg {
  width: 250px;
  height: 260px;
  position: absolute;
  top: -55px;
  right: -15px;
  z-index: 1;
}

.fanta {
  width: 95px;
  height: 125px;
  position: absolute;
  bottom: -32px;
  right: 160px;
  z-index: 3;
}

.smallb {
  position: absolute;
  width: 75px;
  height: 85.98px;
  bottom: -25px;
  right: 110px;
  z-index: 2;
  transform: rotate(-4.03deg);
}
