.CartWrapper {
  display: flex;
  flex-direction: column;
  width: 1440px;
  min-height: 700px;
  background-color: rgb(247, 246, 246);
}

.CartHeader {
  display: flex;
  align-items: center;
  width: 1440px;
  height: 120px;
  background-color: #fff;
}

.CartTitle {
  font-size: 28px;
  font-family: "Josefin Sans";
  margin-left: 50px;
}

.CartProducts {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 300px;
  width: 600px;
  min-height: 150px;
  background-color: #fff;
}
.CartProductsHeader {
  background-color: rgb(247, 246, 246);
  display: flex;
  align-items: center;
  margin-left: 300px;
  justify-content: space-around;
  width: 400px;
  height: 100px;
}
.CartProductsTitle {
  font-size: 24px;
  font-family: "Josefin Sans";
}
.CartProductsAmount {
  font-size: 18px;
  color: rgb(121, 121, 121);
  font-family: "Josefin Sans";
}
.CartProduct {
  display: flex;
  justify-content: space-between;
  width: 500px;
  height: 60px;
  background-color: #fff;
}
.CartButtonWrapper {
  margin-top: 20px;
}
.CartButton {
  background-color: #fff;
  border: none;
  font-size: 18px;
  color: rgb(121, 121, 121);
}

.HoverBlue:hover {
  /* font-size: 20px; */
  color: rgb(0, 120, 190);
  cursor: pointer;
}
.HoverRed:hover {
  color: red;
  cursor: pointer;
}

.CartBackArrow {
  text-decoration: none;
  margin-right: 300px;
}
